.signUpScreen {
    max-width: 300px;
    padding: 70px;
    margin-left: auto;
    margin-right: auto;
    background-color: rgba(0, 0, 0, 0.85);
    border-radius: 5px;
}

.signUpScreen>form {
    display: grid;
    flex-direction: column;
    gap: 14px;
}

.signUpScreen>form>input {
    outline-width: 0;
    border-radius: 5px;
    height: 40px;
    border: none;
    padding: 5px 15px;
}

.signUpScreen>form>button {
    padding: 16px 20px;
    font-size: 1rem;
    color: #fff;
    border-radius: 5px;
    background-color: #e50914;
    font-weight: 600;
    border: none;
    cursor: pointer;
    margin-top: 20px;
}

.signUpScreen>form>h1 {
    text-align: left;
    margin-bottom: 20px;
}

.signUpScreen>form>h4 {
    text-align: left;
    margin-top: 10px;
}

.signUpScreenGray {
    color: gray;
}

.signUpScreenLink:hover {
    cursor: pointer;
    text-decoration: underline;
}